<template>
  <div class="id_card_upload">
    <div class="pay_way_title">收藏加购截图</div>
    <van-row class="upload_hint text-desc">
      <van-icon name="hint"/>
      温馨提示: 请保证图片清晰显示, 否则无法通过审核
    </van-row>
    <van-row gutter="20" class="id_card_row upload_box">
      <van-col span=24>
        <div class="upload_wrap" @click="fileSelect(1)">
          <div v-if="screenshot_search">
            <img :src="screenshot_search">
          </div>
          <div class="add_btn" v-else>
            <van-icon name="add" />
            <div>上传照片</div>
          </div>
        </div>
      </van-col>
 <!--     <van-col span=12>
        <div class="upload_wrap" @click="fileSelect(2)">
          <div v-if="screenshot_pay">
            <img :src="screenshot_pay">
          </div>
          <div class="add_btn" v-else>
            <van-icon name="add" />
            <div>上传照片</div>
          </div>
        </div>
      </van-col>-->
    </van-row>
    <van-row gutter="10" class="id_card_row">
      <van-col span=24>
        <div class="text-desc red_color">收藏加购截图</div>
        <img src="../../../assets/images/search.jpg" width="50%">
      </van-col>
<!--      <van-col span=12>
        <div class="text-desc">付款截图</div>
        <img src="../../../assets/images/123.jpg" width="50%">
      </van-col>-->
    </van-row>
    <input @change="fileChanged" type="file" accept="image/*" ref="file" style="display: none" />
    <div v-if="!viewOnly" class="register-btn">
      <van-button size="large" :loading="isLoading" @click="next" type="danger">
        提交
      </van-button>
    </div>
  </div>
</template>

<script>
  import { Row, Col, ImagePreview} from 'vant';
  export default {
    name: "orderUpload",
    data() {
      return{
        isLoading: false,
        files : [],
        imgIndex : 0,
        screenshot_search:null,
        // screenshot_pay:null
      }
    },
    props: {
      id: this.id
    },

    methods: {

      fileSelect(i) {
        if(this.readonly){
          ImagePreview([this.screenshot_search], 0);
          return;
        }
        this.imgIndex = i;
        this.$refs.file && this.$refs.file.click();
      },
      fileChanged(e) {
        let list = this.$refs.file.files;
        if (this.disabled || !list.length) {
          return;
        }
        let f = list[0];
        if (!this.isContain(f)) {
          const item = {
            name: f.name,
            size: f.size,
            src : '',
            file: f
          };
          this.html5Reader(f, item);
          this.files.push(item);
        }
        this.$refs.file.value = ''
      },
      // 将图片文件转成BASE64格式
      html5Reader(file, item){
        const reader = new FileReader();
        reader.onload = (e) => {
          item.src = e.target.result;
          if(this.imgIndex == 1){
            this.screenshot_search = item.src;
          }else{
            this.screenshot_pay = item.src;
          }
        };
        reader.readAsDataURL(file);
      },
      isContain(file) {
        return this.files.find((item) => item.name === file.name && item.size === file.size);
      },
      submit(){
        if (!this.files || this.files.length <= 0) {
          Toast.fail("请选择图片");
          return;
        }
        let formdata = new FormData();
        formdata.append("order_id", this.id);
        formdata.append("img", this.files[0].file);
        this.isLoading = true
        this.$reqPost(COLLECT_UPLOAD, formdata, {
          hideLoading: true
        }).then(res => {
          this.submitSuccess(res);
        }).catch(err => {
          this.isLoading = false
        });
      },
      submitSuccess(res) {
        var tipStr=`<p style="text-align: center;">提交成功，商家会在24小时内完成审核，审核后才能进行下单。</p>
            <p style="color: darkred; text-align: center;">（请勿直接拍下付款订单）</p>`

        Dialog.alert({
          message:tipStr
        }).then(() => {
          this.$router.push({ path: "/orders"})
        }).catch((err) => {
          // console.log(err);
        });
      }
    },

    components: {
      [Row.name]: Row,
      [Col.name]: Col,
      [ImagePreview.name]: ImagePreview
    }
  }
</script>


<style lang="scss" scoped>

  .pay_way_title {
    font-weight: bold;
  }

  .id_card_upload{
    margin: 10px 0 30px 0;
    background-color: #fff;
    padding: 15px;
    >div{
      margin-bottom: 15px;
    }
  }
  .upload_hint{
    position: relative;
    padding-left: 20px;
    line-height: 1.6;
    i{
      position: absolute;
      top: 2px;
      left: 0;
    }
  }

  .id_card_row{
    >div{
      text-align: center;
    }
    .text-desc{
      margin-bottom: 8px;
    }
    .red_color {
      color: darkred;
      font-size: 14px;
      font-weight: 700;
    }
  }

  .upload_box .upload_wrap{
    position: relative;
    border: 1px dashed $gray;
    min-height: 100px;
    box-sizing: border-box;
    padding: 5px;
    text-align: center;
    img{
      max-width: 100%;
      height: 120px;
    }
    .add_btn{
      position: absolute;
      top: 50%;
      left: 50%;
      font-size: 20px;
      color: $gray;
      transform: translate(-50%, -50%);
      i{
        font-size: 24px;
      }
    }
  }

  .register-btn {
    padding: 0px 10px;
  }
</style>



